<template>
  <div style='z-index: 2014'>
    <van-overlay :show='showOverlay'>
      <div class="wrapper" @click="hide">
        <img  class="top" :src="`${$cdnImageUrl}/leadingshare/img-arrow.png`"
             alt="">
        <img class="bottom" :src="`${$cdnImageUrl}/leadingshare/btn-know.png`"
             alt="">
      </div>
    </van-overlay>
  </div>
</template>

<script>
import utils from '@/utils/utils'
export default {
  name: 'leadingShare',
  props: ['shareInfo', 'value'],
  data() {
    return {
      showOverlay: this.value
    }
  },
  methods: {
    hide() {
      this.showOverlay = false;
      this.$emit('input', false)
    }
  },
  mounted() {
    // postmessage
    const that = this
    utils
      .loadUniMPEnvDeps()
      .then(() => {
        uni.postMessage({
          data: {
            type: ['share'],
            share: that.shareInfo,
          },
        })
      })
      .catch((e) => {
        // 拉取失败处理
        console.log(e)
      })
  },
}
</script>
