            <div class='flex-middle'
                 style='justify-content:center;padding:1rem 0rem 0rem 0rem;overflow:hidden;position:relative;height:100%;width:100%;'>
              <img v-if='popupType===6'
                   src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_star.png"
                   style='width:3.38rem'
                   alt="">
              <div v-if='popupType===7'
                   style='width:2.88rem;color:#DB3232;font-size:0.24rem;line-height:0.36rem;text-align:center'>我已集齐助力，感谢您的帮助，一起来抢丰厚大奖吧！</div>
              <div v-if='popupType===8'
                   style='width:2.88rem;color:#DB3232;font-size:0.24rem;line-height:0.36rem;text-align:center'>您已成功助力，每人最多助力一次哦！</div>
            </div>
          </div>
          <img v-if='popupType===6||popupType===8'
               @click='fnConfirm'
               class="button-img"
               style='width:3.6rem;position:relative;top:-0.3rem'
               src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_confirm.png"
               alt="">
          <img v-if='popupType===7'
               @click='fnConfirm'
               class="button-img"
               style='width:3.85rem;position:relative;top:-0.3rem'
               src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_goplay.png"
               alt="">
          <div class="pop-bottom"
               @click="showPopup=false">
          </div>
        </div>
        <div v-if="popupType>=9&&popupType<=11"
             class="type-0 flex-middle">
          <div style='align-self:flex-end;'
               @click="showPopup=false">
            <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/close_little.png"
                 style='width:0.56rem;'
                 alt="">
          </div>
          <div v-if='popupType===9'
               class="bg2">
            <div style='padding-top:1rem;padding-left:0.4rem;padding-right:0.4rem;font-size:0.24rem;line-height:0.36rem;color: #666666;'>海尔智家幸运大转盘，天天福利多！免费参与抽丰厚大奖，下一个幸运的小可爱就是你！</div>
            <img @click='fnDoHelp'
                 src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_help.png"
                 style='width:3.6rem;margin-top:0.3rem;'
                 alt="">
            <img @click='fnConfirm'
                 src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_iplay.png"
                 style='width:3.6rem;margin-top:0.3rem;'
                 alt="">
          </div>
          <div v-if='popupType===10'
               class="bg3">
            <div style='height:6.36rem;padding-left:0.4rem;padding-right:0.4rem;font-size:0.24rem;line-height:0.36rem;color: #666666;  overflow: scroll;'
                 v-html="gameInfo.activityDesc"></div>
          </div>
          <div v-if='popupType===11'
               class="bg4">
            <div style='padding-left:0.4rem;padding-right:0.4rem;font-size:0.24rem;line-height:0.36rem;color: #666666;overflow:scroll;height:1rem'
                 v-html="helpInfo.helpDesc"></div>
            <img @click='fnShareWechat(true)'
                 src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_invite.png"
                 style='width:3.6rem;margin-top:0.3rem;'
                 alt="">
            <div style='margin-top:0.1rem;padding-left:0.4rem;padding-right:0.4rem;font-size:0.2rem;line-height:0.2rem;color: #999;margin-top:0.1rem; '>好友助力，最多额外获得<span style="color:#F32E3F">{{friendsHelp.canTakeMoreChance}}</span>次抽奖机会</div>
            <div style="margin-top:1.7rem;display:flex;align-items:center;">
              <div style='font-size:0.24rem;color:#333;vertical-align:bottom;position:relative;top:0.2rem;'>抽奖机会</div>
              <!-- 进度条 -->
              <div class='progresser'
                   ref='progresser'>
                <div v-for="(item,index) in stepList"
                     :key='index'
                     style='display:flex;flex-direction:column;justify-content:flex-end;align-items:center;overflow:visible;'>
                  <div v-if="index<=friendsHelp.alreadyTake"
                       style='position:relative;left:-0.15rem;'>
                    <span class="counter">{{item.isDone?`已集齐${friendsHelp.everyStep}人`:`${friendsHelp.current}人`}}</span>
                  </div>
                  <div style='display:flex;align-items:center;'>
                    <div style='background-color: #eee;width:1.1rem;height:0.1rem;'
                         :class='index===0?"dzp-border-left":""'>
                      <div :style='{width: `${item.progress*100}%`}'
                           class="progress"
                           :class='index===0?"dzp-border-left":index===friendsHelp.alreadyTake?"dzp-border-right":""'> </div>
                    </div>
                    <div :class="index<friendsHelp.alreadyTake?'dzp_slider_button':'dzp_slider_button_gray'">+{{index+1}}</div>
                  </div>
                </div>
              </div>
            </div>
            <div style='margin-top:0.2rem;padding-left:0.4rem;padding-right:0.2rem;font-size:0.2rem;line-height:0.2rem;color: #999;margin-top:0.1rem; '>已额外获得<span style="color:#F32E3F">{{friendsHelp.alreadyTake}}</span>次机会<template v-if='(this.canShareHelp && this.helpInfo.chance !== this.helpInfo.hasChanceNum)'>，还差<span style="color:#F32E3F">{{friendsHelp.everyStep - friendsHelp.current}}</span>人助力即可获得一次额外机会！</template></div>
          </div>
        </div>
      </van-popup>
      <LeadingShare v-if="showLeadingShare"
                    v-model="showLeadingShare"
                    :shareInfo="shareInfo" />
    </div>
  </div>
</template>

<script>
import { getGameInfo, gameLuckDraw, gameWinnerList, gameHelp, gameHelpInfo } from '@/http/service/game'
import utils from '@/utils/utils'
import { Dialog, Toast } from 'vant'
import ClipboardJS from 'clipboard'
import skeleton from '@/components/skeleton/index.vue'
import LeadingShare from '@/components/MPLeadingShare/index.vue'
import URL from '@/http/url'

import { mapGetters } from 'vuex'
import md5 from '@/utils/md5'
let _intervalF
let defaultPrize // 默认为谢谢惠顾，防止异常情况无法抽奖
export default {
  name: 'App',
  data() {
    return {
      isSkeletonLoading: false, //
      canShare: false, // 客户端根据需求来决定是否关闭分享，比如因某种原因需暂时屏蔽分享功能
      canShareHelp: false, // 是否助力分享
      gameId: '',
      prizeInfo: {}, // 中奖详情
      isRolling: false,
      gameInfo: {}, // 游戏详情
      showPopup: false, // 弹出层
      popupType: -1, // 弹出框类型 [0 未中奖, 1 中奖普通, 2中奖 填写地址, 3中奖优惠券, 4免费机会用完, 5机会用完, 6助力成功，7我已集齐，8您已助力，9我要参加，10活动规则，11邀请助力 ]
      icons: [
        'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_start_1.png',
        'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_start_2.png',
        'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/dzp_start_gray.png',
      ],
      currentIndex: 0,
      list: [], // 奖品列表
      animationStop: true, // 动画
      target: -1, // 中奖号码
      imgBaseUrl: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/', // 图片baseURL
      shotChanges: 0, // 剩余次数
      buttonAnimation: false, // 控制按钮动画
      animationFlag: true, // 控制按钮图片展示,
      winnerList: [],
      openId: '',
      helpInfo: {
        chance: 0,
        hasChanceNum: 0,
        helpNum: 0,
        helpedNum: 0,
      }, // 助力信息
      totalPartNum: 0, // 默认次数+已获得的助力次数
      playTimes: 0, // 已有效参与次数
      partNum: 0, // 默认次数
      gameStatusType: -1, // -1 : 未开始； -2 : 已结束
      isAliENV: false,
      isAliMP: false,
      identityCode: undefined,
      shareInfo: {},
      showLeadingShare: false,
    }
  },
  components: { skeleton, LeadingShare },
  computed: {
    ...mapGetters(['mId']),
    canPlay() {
      if (this.identityCode === '101') {
        return true
      }
      return false
    },
    stepList() {
      const arr = []
      for (var index = 0; index < this.helpInfo.chance; index++) {
        let pg = index < this.friendsHelp.alreadyTake ? 1 : index === this.friendsHelp.alreadyTake ? this.friendsHelp.current / this.friendsHelp.everyStep : 0
        arr.push({
          index: index,
          isDone: index < this.friendsHelp.alreadyTake,
          progress: pg,
        })
      }
      return arr
    },
    winnerListDuration() {
      return {
        'animation-duration': `${this.winnerList.length < 5 ? 5 : this.winnerList.length}s`,
        '-webkit-animation-duration': `${this.winnerList.length < 5 ? 5 : this.winnerList.length}s`,
      }
    },
    friendsHelp() {
      // 计算滚动位置
      // height = xxxpx;
      if (!this.helpInfo) {
        return {
          canTakeMoreChance: 0,
          alreadyTake: 0,
          everyStep: 0,
          current: 0,
        }
      }
      return {
        canTakeMoreChance: this.helpInfo.chance | 0,
        alreadyTake: this.helpInfo.hasChanceNum | 0,
        everyStep: this.helpInfo.helpNum | 0,
        current: this.helpInfo.helpedNum % this.helpInfo.helpNum | 0,
      }
    },
  },
  activated() {
    // 获得游戏信息
    this._hidePopup()
    const _query = this.$route.query
    const that = this
    this.gameId = _query.gameId
    // 打开时如果为助力链接
    if (_query.isHelp && _query.isHelp === 'true') {
      // 获得微信授权openId
      if (utils.isWeixin()) {
        if (_query.code) {
          // 获取openId
          that.$http.get(URL.WX_CODE_TO_PAYINFO, { params: { code: _query.code } }).then(({ data }) => {
            that.openId = data.data.openid
            // 弹窗
            that.popupType = 9
            that.showPopup = true
          })
        } else {
          // 获取授权
          that._doGetWxCode()
        }
      } else {
        Toast('请在微信环境打开链接助力')
      }
    }
    this.isAliENV = navigator.userAgent.includes('AliApp')
    if (this.isAliENV) {
      utils.loadScript('https://appx/web-view.min.js').then(() => {
        my.getEnv((res) => {
          console.log(`是否为小程序环境：${res.miniprogram}`)
          if (res.miniprogram) {
            this.isAliMP = true
            // 查询支付宝小程序登录状态
            my.postMessage({ event: 'checkLogin' })
            // 接受支付宝小程序回传的用户信息
            my.onMessage = function (e) {
              console.log(e)
              if (!utils.isNull(e) && !utils.isNull(e.event) && (e.event === 'checkLogin' || e.event === 'login')) {
                if (!utils.isNull(e.result)) {
                  if (!utils.isNull(e.result.userInfo)) {
                    const msg = { ...e.result.userInfo.userMsg }
                    if (!msg.sessionValue) {
                      msg.sessionValue = msg.token
                    }
                    that.$store.dispatch('sgRnAccount/doLogin', msg)
                  } else {
                    that.$store.dispatch('sgRnAccount/doLogin', {})
                  }
                  if (_query) {
                    this._doGetHelpInfo()
                    that.getGameInfo()
                  }
                }
              }
              // console.log(e); // {'sendToWebView': '1'}
            }
          }
        })
      })
    }

    // 轮询中奖人列表
    this._fetchWinnerList()
    _intervalF = setInterval(this._fetchWinnerList, 20000)

    if (_query) {
      // 获取游戏信息和助力信息
      this._doGetHelpInfo()
      this.getGameInfo()
    } else {
      Toast('无游戏信息')
    }
  },
  beforeMount() {},
  beforeDestroy() {
    clearInterval(_intervalF)
  },
  mounted() {
    this.$util.gioTrackWithVariable('mall_wheel_view')
    if (utils.isScMp()) {
      this.postDefaultWechatShareMessage()
    }
  },
  methods: {
    getPreLocationPath() {
      const location = window.location
      const _query = this.$route.query
      const gameId = _query.gameId
      return location.origin + location.pathname + `?gameId=${gameId}`
    },
    /**
     * 发送微信默认分享链接 (微信直接进入大转盘页面时的兼容分享逻辑)
     */
    postDefaultWechatShareMessage() {
      const that = this
      if (!this.$store.getters.getLoginStatus) {
        const url = this.getPreLocationPath()
        this.shareInfo = { path: url }
      } else {
        const url = this.getPreLocationPath() + `&memberId=${this.mId}`
        this.shareInfo = { path: url }
      }
      utils
        .loadUniMPEnvDeps()
        .then(() => {
          uni.postMessage({
            data: {
              type: ['share'],
              share: that.shareInfo,
            },
          })
        })
        .catch((e) => {
          // 拉取失败处理
          console.log(e)
        })
    },
    /**
     * 获取游戏信息
     */
    getGameInfo() {
      const that = this
      getGameInfo({ gameId: this.gameId })
        .then((res) => {
          const result = res.data
          console.log(result)
          if (result.success) {
            that.identityCode = result.data.identityCode
            // 剩余次数
            that.shotChanges = result.data.totalPartNum - result.data.playTimes
            // that.shotChanges = 10;
            that.totalPartNum = result.data.totalPartNum
            that.playTimes = result.data.playTimes
            that.partNum = result.data.partNum

            // gameInfo
            that.gameInfo = result.data.game
            that.canShareHelp = result.data.partType === 2
            if (that.gameInfo.beginTime * 1000 > Date.now()) {
              Toast('活动暂未开始')
              that.shotChanges = -1
              that.gameStatusType = -1
            } else if (that.gameInfo.endTime * 1000 < Date.now()) {
              Toast('活动已经结束')
              that.shotChanges = -1
              that.gameStatusType = -2
            } else {
              that.gameStatusType = 0
              that.startButtonAnimation()
            }
            // 重新排序
            let map = { 11: 4, 4: 5, 10: 6, 5: 7, 9: 8, 8: 9, 7: 10, 6: 11 }
            const arr = []
            for (let index = 0; index < 12; index++) {
              arr.push({
                number: index,
                order: map[index] ? map[index] : index,
                info: result.data.gameImg.find((item) => item.postion === index + 1),
              })
            }
            that.list = arr
            defaultPrize = result.data.gameImg.find((v) => v.prizeType === 3)
            this.isSkeletonLoading = true
          }
        })
        .catch((e) => {
          Toast(e.message)
          this.isSkeletonLoading = true
        })
    },
    // 计算助力滚动位置
    _scrollProgresser() {
      if (!this.helpInfo) {
        return
      }
      var height = 0
      if (this.helpInfo.hasChanceNum >= 2) {
        height = (this.helpInfo.hasChanceNum + 1) * 1.55 - 4.2
      }
      if (this.$refs.progresser) {
        this.$nextTick(() => {
          this.$refs.progresser.scrollLeft = 50 * height
        })
      }
    },
    fnDoHelp() {
      // 根据openId助力
      const that = this
      const _query = this.$route.query
      gameHelp({ openid: this.openId, memberId: _query.memberId, channel: '1', gameId: this.gameId })
        .then((res) => {
          const result = res.data
          if (result.success) {
            const message = result.message
            if (message.indexOf('助力成功') !== -1) {
              that.popupType = 6
            } else if (message.indexOf('您已助力') !== -1) {
              that.popupType = 8
            } else if (message.indexOf('已达') !== -1) {
              that.popupType = 7
            } else {
              Toast(message)
            }
          } else {
            Toast(result.message)
          }
        })
        .catch((e) => {
          Toast(e.message)
        })
    },
    // 隐藏弹窗
    _hidePopup() {
      this.showPopup = false
      this.popupType = -1
    },
    // 跳转到我的优惠券
    fnGotoMyCoupon() {
      this._hidePopup()
      this.$router.push('/coupon')
    },
    // 跳转到我的钻石
    fnGotoMyDiamond() {
      this._hidePopup()
      this.$router.push('/diamond')
    },
    fnConfirm() {
      this._hidePopup()
    },
    _login() {
      this.$store.dispatch('doLogin', this.getPreLocationPath()).then((res) => {
        if (utils.isHaierApp() && res !== 400) {
          window.location.reload()
        }
      })
    },
    // 分享
    fnShareWechat(canHelp = true) {
      if (utils.isSgRn() || utils.isHaierApp()) {
        const url = this.getPreLocationPath() + `&memberId=${this.mId}&isHelp=${canHelp}`
        utils.inAppShare({ platformType: 'WechatSession', shareType: 'webPage', url: url, title: this.gameInfo.title })
      } else if (this.isAliENV && this.isAliMP) {
        // 支付宝小程序分享或跳转登录页
        my.postMessage({ event: this.$store.getters.getLoginStatus ? 'share' : 'login' })
      } else if (utils.isScMp()) {
        const url = this.getPreLocationPath() + `&memberId=${this.mId}&isHelp=${canHelp}`
        this.shareInfo = { path: url }
        this.showLeadingShare = true
      } else {
        // const url = location.href + (location.href.indexOf('?') !== -1 ? '&' : '?') + `&memberId=${this.mId}&isHelp=${canHelp}`
        const originURL = window.location.href
        const oURLArr = originURL.split('?')
        if (oURLArr.length >= 1) {
          const path = oURLArr[0]
          const query = `?gameId=${this.gameId}&memberId=${this.mId}&isHelp=${canHelp}`
          const url = path + query
          this._copy(url)
        } else {
          console.log('暂不支持拷贝功能')
        }
      }
    },
    /**
     * @description  复制链接
     */
    _copy(txt) {
      let context = this
      const a = document.createElement('a')
      const clipboard = new ClipboardJS(a, {
        text: function (trigger) {
          return txt
        },
      })
      clipboard.on('success', () => {
        // Toast('复制成功，请到微信内分享');
        context.$toast.success('复制成功，请到微信内分享')
      })
      clipboard.on('error', () => {
        context.$toast.success('复制失败')
      })
      a.click()
    },
    _doGetWxCode() {
      let redirectUrl = location.href
      utils.getWxCode('wxb03a523baf487993', redirectUrl)
    },
    _doGetHelpInfo() {
      const that = this
      gameHelpInfo({ gameId: this.gameId, memberId: this.mId })
        .then((res) => {
          that.helpInfo = res.data.data
          that._scrollProgresser()
        })
        .catch((e) => {
          Toast(e.message)
        })
    },
    _fetchWinnerList() {
      gameWinnerList({ gameId: this.gameId })
        .then((result) => {
          const res = result.data
          if (res.success) {
            this.winnerList = res.data
          } else {
            Toast(res.message)
          }
        })
        .catch((e) => {
          // Toast(e.message);
          console.log(e.message)
        })
    },
    fnRecord() {
      if (!this.$store.getters.getLoginStatus) {
        if (this.isAliENV && this.isAliMP) {
          // 支付宝小程序分享或跳转登录页
          my.postMessage({ event: 'login' })
          return
        }
        this._login()
        return
      }
      this.$router.push(`/games/giftRecord?gameId=${this.$route.query.gameId}`)
    },
    fnAddress() {
      if (!this.$store.getters.getLoginStatus) {
        if (this.isAliENV && this.isAliMP) {
          // 支付宝小程序分享或跳转登录页
          my.postMessage({ event: 'login' })
          return
        }
        this._login()
        return
      }
      // this.$router.push(`/games/giftAddress`)
      this.$router.push(`/addressEdit?gameId=${this.$route.query.gameId}`)
    },
    // 到填写地址页面
    gotoAddress() {
      //  gameId: _query.gameId,
      // uuid: _query.uuid,
      // prizeId: _query.prizeId,
      this._hidePopup()
      this.$router.push(`/addressEdit?gameId=${this.$route.query.gameId}&uuid=${this.prizeInfo.uuid}&prizeId=${this.prizeInfo.gamePrizeId | ''}`)
    },
    fnBack() {
      this.$store.dispatch('doHistoryBack')
    },
    showGameRules() {
      this.popupType = 10
      this.showPopup = true
    },
    showShare() {
      this.$util.gioTrackWithVariable('mall_wheel_click_share')
      if (!this.canShareHelp) {
        // 暂时屏蔽 分享功能 DSG-29560
        // this.fnShareWechat(false);
      } else {
        // 每次打开更新一次
        this._doGetHelpInfo()
        this.popupType = 11
        this.showPopup = true
      }
    },
    // 抽奖按钮动画
    startButtonAnimation() {
      if (!this.buttonAnimation && this.shotChanges > 0) {
        this.buttonAnimation = true
        window.requestAnimationFrame(this.buttonAnimate.bind(this, new Date()))
      }
    },
    // 暂停抽奖按钮动画
    stopButtonAnimation() {
      this.buttonAnimation = false
    },
    // 抽奖按钮动画控速
    buttonAnimate(pre) {
      const now = new Date()
      const shouldNext = now - pre > 500
      if (shouldNext) {
        this.animationFlag = !this.animationFlag
        pre = now
      }
      if (this.buttonAnimation) {
        window.requestAnimationFrame(this.buttonAnimate.bind(this, pre))
      }
    },
    // 开始抽奖
    startRoll() {
      const path = this.$route.fullPath
      const that = this
      if (!this.$store.getters.getLoginStatus) {
        if (this.isAliENV && this.isAliMP) {
          // 支付宝小程序分享或跳转登录页
          my.postMessage({ event: 'login' })
          return
        }
        this._login()
        return
      }
      // 已过期或未开始的活动
      if (this.shotChanges === -1) {
        return
      }
      // 不符合身份
      if (!this.canPlay) {
        Dialog.alert({
          title: '温馨提示',
          message: '请升级成平台合伙人后再参与游戏，具体可注册/登录智家App，咨询在线客服',
        }).then(() => {
          // on close
        })
        return
      }
      if (this.shotChanges <= 0) {
        // 4 免费用完 5 全部用完
        if ((this.canShareHelp && this.helpInfo.chance === this.helpInfo.hasChanceNum) || !this.canShareHelp) {
          this.popupType = 5
        } else {
          this.popupType = 4
        }
        this.showPopup = true
        return
      }
      if (this.isRolling) return
      this.isRolling = true
      this.$util.gioTrackWithVariable('mall_wheel_click_game')
      // 直接开启转动动画
      this.beginRollAnimation()

      const timestamp = Date.now()
      const sign = md5.hex_md5(`${this.mId}${timestamp}`)
      // 抽奖接口
      gameLuckDraw({ gameId: this.gameId, timestamp, sign })
        .then((res) => {
          let result = res.data
          if (result.success && result.result.prizeType > 0) {
            const info = result.result
            this.target = info.postion - 1
            this.shotChanges = info.surplusChance
            this.prizeInfo = info // 中奖信息
            const prizeType = Number(info.prizeType)
            this.popupType = prizeType === 1 ? 2 : prizeType === 2 || prizeType === 6 ? 3 : prizeType === 5 ? 1 : 0 // 1：实物，2：优惠券，3：谢谢参与，5：售后/权益/心愿类奖品 6：钻石  |对照关系|  0 未中奖, 1 中奖普通, 2中奖 填写地址, 3中奖优惠券
            // this.beginRollAnimation()
          } else {
            // prizeType 返回 -1 代表 抽奖异常，更新抽奖次数
            if (result && result.result && result.result.prizeType < 0) this.shotChanges = result.result.surplusChance

            if (result.errorCode === '-100' || result.errorCode === -100) {
              if (this.isAliENV && this.isAliMP) {
                // 支付宝小程序分享或跳转登录页
                my.postMessage({ event: 'login' })
                return
              }
              this.$store.dispatch('doLogin', this.getPreLocationPath()).then((res) => {
                if (res === 400) {
                  console.log('请求异常')
                  return
                }
                console.log(res)
                that.$router.push(path)
              })
            } else {
              this.prizeInfo = defaultPrize
              this.target = this.prizeInfo.postion - 1
              // Toast(result.message);
            }
          }
        })
        .catch((e) => {
          this.prizeInfo = defaultPrize
          this.target = this.prizeInfo.postion - 1
          // Toast(e.message)
        })
    },
    // 抽奖动画
    beginRollAnimation() {
      window.requestAnimFrame = (function () {
        return (
          window.requestAnimationFrame ||
          window.webkitRequestAnimationFrame ||
          window.mozRequestAnimationFrame ||
          function (callback) {
            window.setTimeout(callback, 1000 / 60)
          }
        )
      })()

      if (this.animationStop) {
        this.animationStop = false
        this.stopButtonAnimation() // 按钮停止闪动
        const speed = 350 // 初始速度
        window.requestAnimationFrame(this.speedControl.bind(this, new Date(), speed, true))
      }
    },
    // 抽奖动画控速
    speedControl(pre, speed, isSpeedUp) {
      const minSpeed = 350 // 控制最小移动速度
      const maxSpeed = 15 // 控制最小移动速度
      if (this.target === this.currentIndex && speed > minSpeed) {
        this.endofRoll()
      } else if (!this.animationStop) {
        const now = new Date()
        const shouldNext = now - pre > speed
        if (shouldNext) {
          this.nextStep()
          pre = now
          if (isSpeedUp) {
            speed /= 1.1 // 指数加速
          } else {
            speed *= 1.06 // 指数减速
          }
        }
        window.requestAnimationFrame(this.speedControl.bind(this, pre, speed, isSpeedUp ? speed > maxSpeed : false))
      }
    },
    // 间隔帧
    nextStep() {
      if (this.currentIndex === 11) {
        this.currentIndex = 0
      } else {
        this.currentIndex++
      }
    },
    // 滚动停止时 调用
    endofRoll() {
      this.animationStop = true
      const prizeType = Number(this.prizeInfo.prizeType)
      this.popupType = prizeType === 1 ? 2 : prizeType === 2 || prizeType === 6 ? 3 : prizeType === 5 ? 1 : 0 // 1：实物，2：优惠券，3：谢谢参与，5：售后/权益/心愿类奖品 6：钻石  |对照关系|  0 未中奖, 1 中奖普通, 2中奖 填写地址, 3中奖优惠券
      // this.popupType = this.prizeInfo.prizeType === 1 ? 2 : this.prizeInfo.prizeType === 2 ? 3 : this.prizeInfo.prizeType === 5 ? 1 : 0;// 1：实物，2：优惠券，3：谢谢参与，5：售后/权益/心愿类奖品  |对照关系|  0 未中奖, 1 中奖普通, 2中奖 填写地址, 3中奖优惠券
      this.startButtonAnimation()
      setTimeout(() => {
        this.showPopup = true
        this.isRolling = false
      }, 500)
    },
  },
  filters: {
    replaceMobile(pre) {
      return (pre + '').replace(/(\d{3})\d{4}(\d*)/, '$1****$2')
    },
  },
  watch: {
    '$store.state.isLogin': function (newValue, oldValue) {
      window.location.reload()
    },
  },
}
</script>

